.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4rem;
    min-height: 100vh;
    background-color: #f5f5f5;

    .login-container {
        width: 100%;
        max-width: 400px;
        padding: 2rem;
        background-color: #ffffff;
        border-radius: 0.5rem;
        box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);

        h1 {
            font-size: 2.4rem;
            margin-bottom: 2rem;
            text-align: center;
            color: #2c3e50;
        }

        .input-wrapper {
            display: flex;
            flex-direction: column;
            margin-bottom: 1.5rem;

            label {
                margin-bottom: 0.5rem;
                font-weight: 600;
            }

            input {
                padding: 1rem;
                font-size: 1.6rem;
                border: 1px solid #ccc;
                border-radius: 0.25rem;
            }
        }

        .login-button {
            width: 100%;
            padding: 1rem;
            font-size: 1.6rem;
            font-weight: bold;
            background-color: #00bc77;
            color: white;
            border: none;
            border-radius: 0.25rem;
            cursor: pointer;
            transition: background-color 0.3s;

            &:hover {
                background-color: #00995f;
            }
        }

        .error-message {
            color: red;
            font-size: 1.4rem;
            margin-bottom: 1rem;
            text-align: center;
        }
    }
}